import {AUTHENTICAR_USUARIO, DESLOGEAR_USUARIO} from '../actions/types'
 
const authState = {
    auth: []
}

export default function (state = authState, action) {
    switch (action.type) {
        case AUTHENTICAR_USUARIO:
            return {
                ...state,
                auth:[...state.auth, action.payload]
            }
        default:
            return state
    }

    switch (action.type) {
        case DESLOGEAR_USUARIO:
            return {
                ...state
            }
        default:
            return state
    }
}