import { combineReducers } from 'redux';
import authReducer from '../reducers/authReducer'
import usuarioReducer from '../reducers/usuarioReducer'
import loginReducer from '../reducers/loginReducer'
import graficosReducer from '../reducers/graficosReducer'
import firebaseReducer from '../reducers/firebaseReducer'
import proyectosReducer from '../reducers/proyectosReducer'
import proyectoReducer from '../reducers/proyectoReducer'

export default combineReducers({
   auth:authReducer,
   usuario:usuarioReducer,
   renderHeader:loginReducer,
   graficosReducer:graficosReducer,
   firebaseDatos:firebaseReducer,
   proyectosReducer:proyectosReducer, 
   proyecto:proyectoReducer
});