import {AGREGAR_USUARIO, DESLOGEAR_USUARIO} from '../actions/types'
 
const initialState = {
    usuarios: []
}

export default function (state = initialState, action) {
    switch (action.type) {
        case DESLOGEAR_USUARIO:
            return {
                ...state,
                usuarios: [...state.usuarios, action.payload]
            }
        case AGREGAR_USUARIO:
            return {
                ...state,
                usuarios: [...state.usuarios, action.payload]
            }
        default:
            return state
    }
}