import React, {Fragment} from 'react'





//componentes creados para las rutas
import PrivateRouter from './PrivateRouter'
import PublicRouter from './PublicRouter'


//router-dom
import {BrowserRouter as Router,Switch} from 'react-router-dom'

//componentes para las rutas y estaticos
import Login from '../components/login/login'
import Proyecto from '../components/proyectos/Proyecto'
import ListaProyectos from '../components/proyectos/ListaProyectos'
import Header from '../components/layout/Header'
import Dashboard from '../components/dashboard/dashboard'
import RenderRegistro from '../components/registro/renderRegistro'






const Routes = () =>{
    return (
			<Router>
				<Header/>
				<RenderRegistro/>
				<Switch>
					
					<PublicRouter exact path="/login" component={Login}/>
					<PrivateRouter exact path="/dashboard" component={Dashboard}/>
					<PrivateRouter exact path="/proyectos" component={ListaProyectos}/>
					<PrivateRouter exact path="/proyectos/:id" component={Proyecto}/>

				</Switch>
				
				
			</Router>
    )
}


export default Routes