import {OBTENER_PROYECTOS_PREVIEW, RESET_PROYECTOS} from '../actions/types'
 
const proyectosState = {
    proyectos: []
}

export default function (state = proyectosState, action) {
    switch (action.type) {
        case OBTENER_PROYECTOS_PREVIEW:
            return {
                ...state,
                proyectos:[...state.proyectos, action.payload]
            }
        case RESET_PROYECTOS:
            return {
                ...state,
                proyectos:[]
            }
        default:
            return state
    }
}